  width: 160px;
}

#echarts1 {
  width: 90%;
  height: 160px;
}

#echarts2 {
  width: 95%;
  height: 320px;
}

@keyframes rotateLis {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

@keyframes showLis {
  from {
    right: -380px;
  }
  to {
    right: 0px;
  }
}

@keyframes hiddenLis {
  from {
    right: 0px;
  }
  to {
    right: -380px;
  }
}
</style>


